<template>
  <span>
    <MetaFieldHeader
      :title="$t(field_name)"
      :edit_mode.sync="edit_mode"
      :is_saving="is_saving"
      :show_label="show_label"
      @save="updateField"
      @cancel="cancel"
    />
    <div v-if="!edit_mode" v-html="content" />
    <sl-textarea
      v-else
      :help="help ? $t(help) : ''"
      :placeholder="$t('add_text_here')"
      v-sl-model="new_content"
      resize="auto"
      :disabled="is_saving"
      :readonly="!edit_mode"
    />
  </span>
</template>
<script>
export default {
  props: {
    field_name: String,
    show_label: {
      type: Boolean,
      default: true,
    },
    help: String,
    content: {
      type: String,
      default: "",
    },
    path_to_resource: String,
  },
  components: {},
  data() {
    return {
      edit_mode: false,
      is_saving: false,
      new_content: this.content,
    };
  },
  created() {},
  mounted() {},
  beforeDestroy() {},
  watch: {
    edit_mode() {},
    content() {
      this.new_content = this.content;
    },
  },
  computed: {},
  methods: {
    cancel() {
      this.edit_mode = false;
      this.is_saving = false;
      this.new_content = this.content;
    },
    async updateField() {
      this.is_saving = true;
      this.fetch_error = null;

      try {
        // TODO use updateItem

        const response = await this.$axios.patch(this.path_to_resource, {
          [this.field_name]: this.new_content,
        });

        this.response = response.data;
        this.edit_mode = false;
        this.is_saving = false;
      } catch (e) {
        this.is_saving = false;
        this.edit_mode = false;
        this.fetch_error = e.response.data;
      }
    },
  },
};
</script>
<style lang="scss" scoped></style>
